* {
    text-decoration: none;
}
.header {
    /* border: 2px solid green; */
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;
    width: 100%;
}
.button {
    margin: 0px;
    padding: 0px;
    display: none;
}
.header__logo {
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}
.header__menuIcon {
    visibility: hidden;
}
.header__search {
    display: flex;
    flex: 1;
    align-items: center;
}
.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.header__searchInput:focus {
    outline: none;
}
.header__searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.header__optionLineOne {
    font-size: 10px;
}
.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}
.header__optionBasket {
    display: flex;
    align-items: center;
    color: white;
}
.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.header__nav {
    display: flex;
    justify-content: space-evenly;
}
.header__option {
    display: flex;
    flex-direction: column;
    color: white;
    margin-left: 10px;
    margin-right: 10px;
}
.cart {
    display: none;
}

@media screen and (max-width: 320px) {
    .header {
        /* border: 2px solid red; */
        height: 35px;
        display: flex;
        flex-direction: column;
        position: relative;
    }
    .header__logo {
        /* border: 2px solid green; */
        width: 80px;
        position: relative;
        top: -7px;
        right: 107px;
    }
    .header__menuIcon {
        visibility: visible;
        color: white;
        position: absolute;
        top: 9px;
        right: 10px;
        font-size: larger;
    }
    .button {
        display: block;
    }
    .header__nav {
        background-color: #131921;
    }
    .header__search {
        width: 100%;
        background-color: #131921;
        position: relative;
        top: -19px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 9px 0px;
    }
    .header__searchInput {
        width: 76%;
    }
    /* .header__nav {
        visibility: hidden;
        display: flex;
        flex-direction: column;
        width: 100%;
        position: relative;
        top: -57px;
    } */
    .header__nav {
        transition: 0.5s;
        height: 0;
        overflow: hidden;
        display:flex;
        flex-direction: column;
        position: absolute;
        top: 43px;
        width: 100%;
        background-color: #131921;
        /* border-top: 2px solid white; */
    }
    .show-header__nav {
        transition: 0.5s;
        height: 200px;
    }
    .header__optionBasket {
        margin-left: 10px;
    }
    .cart {
        display: block;
        position: absolute;
        top: 10px;
        right: 45px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}