.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
.home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -300px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}
.sponser {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.sponser p {
    color: gray;
    align-items: end;
    width: 60%;
}
.sponser img {
    width: 90%;
}

@media screen and (max-width: 320px) {
    .home {
        margin-top: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .home__row {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0px;
        width: 320px;
    }
    .home__container > img {
        display: none;
    }
}