.checkout {
    display: flex;
    padding: 20px;
    background-color: white;
    height: max-content;
}
.checkout__ad {
    width: 100%;
    margin-bottom: 10px;
    margin-right: 10px;
}
.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}

@media screen and (max-width: 320px) {
    .checkout {
        display: flex;
        flex-direction: column;
        margin-top: 40px;
    }
    .checkout__title {
        font-size: medium;
    }
}
