.product {
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    background-color: white;
    z-index: 1;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    border-radius: 3px;
}
.product:hover {
    transition: 0.3s;
    scale: 1.005;
    box-shadow: 0px 2px 10px 0px #888888
}
.product__rating {
    display: flex;
}
.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product > button {
    background-color: #f0c14b;
    margin-top: 10px;
    border-color: #a88734, #9c7e31, #846a29;
    color: #111;
}
.product__price {
    margin-top: 5px;
}
.product__info  {
    height: 100px;
    margin-bottom: 15px;
}
.add {
    padding: 5px 10px;
    border-radius: 2px;
    border: none;
}
.add:hover {
    cursor: pointer;
}

@media screen and (max-width: 320px) {
    .product {
        width: 80%;
        max-height: 500px;
    }
    
}